const time = 2500;
const step = 1;

function outNum(num, elem) {
  let e = document.querySelector("#counter");
  n = 0;
  let t = Math.round(time / (num / step));
  let interval = setInterval(() => {
    n = n + step;
    if (n == num) {
      clearInterval(interval);
    }
    e.innerHTML = n;
  }, t);
}

outNum(260, "#counter");